// _TD.a.push begin
_TD.a.push(function (TD) {

	TD._msg_texts = {
		"_cant_build": "",
		"_cant_pass": "",
		"entrance": "",
		"exit": "",
		"not_enough_money": " $${0}",
		"wave_info": " ${0} ",
		"panel_money_title": ": ",
		"panel_score_title": ": ",
		"panel_life_title": ": ",
		"panel_building_title": ": ",
		"panel_monster_title": ": ",
		"building_name_wall": "",
		"building_name_cannon": "",
		"building_name_LMG": "",
		"building_name_HMG": "",
		"building_name_laser_gun": "",
		"building_info": "${0}:  ${1} ${2} ${3} ${4} ${5}",
		"building_info_wall": "${0}",
		"building_intro_wall": "  ($${0})",
		"building_intro_cannon": "  ($${0})",
		"building_intro_LMG": "  ($${0})",
		"building_intro_HMG": "  ($${0})",
		"building_intro_laser_gun": "  100% ($${0})",
		"click_to_build": " ${0} ($${1})",
		"upgrade": " ${0}  ${1}  $${2}",
		"sell": " ${0} $${1}",
		"upgrade_success": "${0}  ${1}  $${2}",
		"monster_info": ":  ${0} ${1} ${2} ${3}",
		"button_upgrade_text": "",
		"button_sell_text": "",
		"button_start_text": "",
		"button_restart_text": "",
		"button_pause_text": "",
		"button_continue_text": "",
		"button_pause_desc_0": "",
		"button_pause_desc_1": "",
		"blocked": "",
		"monster_be_blocked": "",
		"entrance_or_exit_be_blocked": "",
		"_": "ERROR"
	};

	TD._t = TD.translate = function (k, args) {
		args = (typeof args == "object" && args.constructor == Array) ? args : [];
		var msg = this._msg_texts[k] || this._msg_texts["_"],
			i,
			l = args.length;
		for (i = 0; i < l; i++) {
			msg = msg.replace("${" + i + "}", args[i]);
		}

		return msg;
	};


}); // _TD.a.push end
